/* 전체 페이지에 적용 */
body, html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* .navbar 스타일 */
.navbar {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  top: 0;
  left: 0;
  background-color: transparent;
  z-index: 10;
  padding: 0; /* 추가: 공백 제거 */
  transition: background-image 0.5s ease;
}

.navbar.scrolled {
  background-image: url('./img/mainImage.jpg'); /* 배경 이미지 경로 설정 */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.navbar-logo {
  flex: 0 0 auto;
  padding: 5px 30px;
}

.navbar p {
  flex: 1;
  margin: 0;
  margin-left: 15%;
  color: white;
  font-weight: bold;
  font-size: 2em;
  white-space: nowrap;
  text-decoration: none;
  cursor: pointer;
}


.navbar-links {
  display: flex;
  list-style-type: none;
  margin-right: 30px;
}

.navbar-links li.active,
.hamburger-links li.active {
    font-weight: bold;
    color: #ff9999; /* 강조 색상 지정 */
    border-bottom: 2px solid #ff9999;
}

.navbar-links li {
  margin-left: 20px;
  font-size: 1.3em;
  color: white;
  text-decoration: none;
  font-weight: bold;
  transition: color 0.3s ease;
}

.navbar-links li:hover {
  text-decoration: underline;
  color: #ff9999;
  cursor: pointer;
}

.hamburger-menu {
  display: none;
}

@media (max-width: 940px) {
  .navbar p {
    display: none; /* 화면 너비가 768px 이하일 때 숨기기 */
  }
}

/* 반응형 디자인 - 모바일 화면 */
@media (max-width: 560px) {
  .navbar-links {
    display: none; /* 작은 화면에서는 기본 내비게이션 링크 숨기기 */
  }

  .hamburger-menu {
    display: block;
    font-size: 1.5em;
    color: white;
    cursor: pointer;
    z-index: 20; /* 햄버거 메뉴 아이콘이 다른 요소보다 앞에 오도록 설정 */
  }

  /* 햄버거 메뉴가 열릴 때 나타나는 링크 스타일 */
  .hamburger-links {
    display: flex;
    flex-direction: column;
    height: 100vh; /* 화면 높이 전체를 차지하도록 설정 */
    position: fixed; /* 화면 스크롤에 고정 */
    top: 0; /* 화면 상단부터 시작 */
    right: 0; /* 화면 오른쪽에 고정 */
    background-color: #2c2c2c;
    list-style: none;
    z-index: 15; /* 다른 요소보다 앞에 오도록 설정 */
    padding-left: 20px;
    padding-right: 20px;
    margin-top: 0px;
  }

  .hamburger-links li {
    margin: 5px 0;
    text-align: left;
    color: #f1f1f1;
  }

  .hamburger-links a {
    color: white;
    font-weight: bold;
    text-decoration: none;
  }
}