/* Contact.css */

.contact-section {
  text-align: center;
}

.main-image {
  width: 100%;
  height: auto;
}

.contact-title {
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 20px;
}

.contact-container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 40px; /* 이미지와 연락처 정보 간의 간격 */
  margin-top: 20px;
  margin-bottom: 20px;
}

.building {
  width: 45%;
  height: auto;
  max-width: 35%;
}

.contact-info {
  display: flex;
  flex-direction: column;
  gap: 20px;
  max-width: 300px;
  align-items: flex-start;
}

.contact-item {
  display: flex;
  align-items: center;
  gap: 15px;
}

.contact-icon {
  font-size: 1.5rem;
  color: #2b2b2b;
}

.contact-name {
  font-weight: bold;
  font-size: 1.1rem;
}
