/* 전체 출판 섹션 스타일 */
.publish-section {
  text-align: center;
  background-color: #f4f4f4;
}

.section-title {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

/* 각 출판 항목 스타일 */
.publication-list {
  list-style: none;
  padding: 0;
  max-width: 800px;
  margin: 0 auto; /* 목록을 가운데로 정렬 */
}

.publication-item {
  padding: 10px 0 0 0;
  text-align: center;
}


.publication-title {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 10px;
  color: #333;
}

.publication-content {
  font-size: 1rem;
  color: #666;
  margin-bottom: 15px;
}


.publication-divider {
  border: none;
  border-top: 1px solid #999;
  margin: 20px 0;
  pointer-events: none;
}

/* 초기 숨김 상태 */
.publication-item.hidden {
  opacity: 0;
  transform: translateY(10px);
  transition: opacity 1s ease-out, transform 1s ease-out;
}

/* 나타나는 상태 */
.publication-item.show {
  opacity: 1;
  transform: translateY(0);
}

.publication-img{
  width: 70%;
}

@media (max-width: 768px) {
  .publication-img {
    width: 80%; /* 모바일 환경에서 이미지 크기 조정 */
    max-width: 250px; /* 모바일에서의 최대 너비 제한 */
  }

  .publication-title {
    font-size: 1rem;
    font-weight: bold;
    margin-bottom: 10px;
    color: #333;
  }

  .publication-content {
    font-size: 0.6rem;
    color: #666;
    margin-bottom: 15px;
  }

}

@media (max-width: 480px) {
  .publication-img {
    width: 70%; /* 작은 화면에서 이미지 크기 더 작게 */
    max-width: 200px; /* 작은 화면에서 최대 너비 제한 */
  }
}