/* Main 컨테이너 스타일 */
.main-content {
  position: relative;
  display: flex;
  width: 100%;
  overflow: hidden;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start; /* 내용이 위에서부터 시작하도록 조정 */
}

/* 화면 좌우로 가득 차게 이미지 설정 */
.main-image-container {
  width: 100%;
  height: 60vh; /* 화면 높이의 60%로 설정 */
  overflow: hidden;
}

.main-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

/* 텍스트 스타일 */
.title {
  font-size: 3vw;
  color: #000;
  margin: 0;
  padding: 10px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
}

.subTitle {
  font-size: 2vw;
  color: #000;
  margin: 10px 0;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5);
}

.description {
  font-size: 1.3vw;
  color: #222;
  text-align: center;
  max-width: 1200px;
  padding: 20px;
  margin-top: 20px;
}


.fade-in {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.6s ease-out, transform 0.6s ease-out;
}

.fade-in.show {
  opacity: 1;
  transform: translateY(0);
}

/* 반응형 디자인 - 작은 화면일 때 */
@media (max-width: 768px) {
  .main-content .title {
    font-size: 5vw;
  }
  .main-content .subTitle {
    font-size: 3.5vw;
  }
  .main-content .description {
    font-size: 3vw;
    padding: 10px;
  }
}

/* Publication 섹션 중앙 정렬 */
.publish {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 1200px;
  margin: 40px auto;
  text-align: center;
}

.publish h2 {
  font-size: 2rem;
  margin-bottom: 20px;
  color: #333;
}

.publish-list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

/* 각 Publication 항목을 3등분으로 배치 */
.publish-item {
  flex: 1 1 calc(30% - 50px); /* 여백을 고려해 3등분 */
  max-width: 400px;
  background-color: #f9f9f9;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.publish-image {
  width: 100%;
  max-width: 150px;
  height: 150px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 10px;
}

.publish-item h3 {
  font-size: 1.2rem;
  color: #333;
  margin-bottom: 10px;
}

.publish-item p {
  font-size: 1rem;
  color: #666;
}

/* 반응형 디자인 - 작은 화면에서의 Publication 배치 */
@media (max-width: 768px) {
  .publish-item {
    flex: 1 1 calc(50% - 20px); /* 가로 2개씩 배치 */
    max-width: 100%;
  }
}

@media (max-width: 480px) {
  .publish-item {
    flex: 1 1 100%; /* 한 줄에 하나씩 */
  }
}

/* 버튼 기본 스타일 */
.view-more-button {
  background-color: #a10e2d; /* 어두운 크림슨 색상 */
  color: white;
  padding: 10px 20px;
  font-size: 1rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
  margin-top: 20px; /* 버튼 위에 여백 추가 */
}

/* 마우스를 올렸을 때 */
.view-more-button:hover {
  background-color: #8b0c26;
  transform: scale(1.05);
}

/* 클릭했을 때 */
.view-more-button:active {
  background-color: #780a21;
  transform: scale(0.95);
}

/* 포커스 시 */
.view-more-button:focus {
  outline: none;
  box-shadow: 0 0 5px #a10e2d;
}
