/* CSS 변경사항 */
.gallery-section {
  text-align: center;
}

.section-title {
  font-size: 2.5em;
  margin-bottom: 20px;
}

.gallery-grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 15px;
  justify-content: center;
  margin: 0 auto;
  max-width: 1000px;
}

.gallery-item {
  position: relative;
  overflow: hidden;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.gallery-image {
  width: 100%;
  height: 70%;
  object-fit: cover;
  transition: transform 0.3s;
}

.gallery-item:hover .gallery-image {
  transform: scale(1.05);
}

/* 이미지 클릭 시 어두워지고 설명 표시 */
.gallery-item.active .gallery-image {
  filter: brightness(50%);
}

.image-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.6);
  color: white;
  padding: 10px;
  font-size: 1em;
}

.image-description {
  font-size: 1em;
  color: white;
  text-align: center;
}

/* 모바일에서 사진 3개만 표시 */
@media (max-width: 768px) {
  .gallery-grid {
    grid-template-columns: repeat(3, 1fr);
    max-width: 100%;
  }
}
