.research-section {
    text-align: center;
    background-color: #f4f4f4;
  }
  
  .section-title {
    align-items: center;
    font-size: 2rem;
    margin-bottom: 20px;
  }
  
  .research-list {
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: center;
  }
  
  .research {
    display: flex;
    align-items: center;
    width: 80%;
    max-width: 800px;
    background-color: #fff;
    padding: 15px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin-bottom: 20px;
  }
  
  .research-image {
    width: 200px; /* 이미지의 고정 너비 */
    height: 300px; /* 이미지의 고정 높이 */
    object-fit: cover; /* 비율을 유지하면서 영역에 맞게 자르기 */
    border-radius: 8px;
    margin-right: 20px;
  }
  
  .research-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: left;
  }
  
  .research-name {
    font-size: 1.5rem;
    font-weight: bold;
    color: #333;
    margin: 0;
  }
  
  .research-description {
    font-size: 1rem;
    color: #666;
    margin-top: 10px;
  }
  
  .no-data-message {
    font-size: 1.2rem;
    color: #888;
    margin-top: 20px;
  }
  

  /* 작은 화면에 대응하는 반응형 스타일 */
@media (max-width: 768px) {
  .research-image {
    width: 150px; /* 모바일에서 이미지 크기 축소 */
    height: 225px;
    margin-right: 15px;
  }

  .section-title {
    font-size: 1.5rem;
  }
}

@media (max-width: 480px) {
  .research {
    flex-direction: column; /* 작은 화면에서 수직 정렬 */
    align-items: center;
    padding: 10px;
  }

  .research-image {
    width: 100px; /* 작은 화면에서 이미지 크기 더 작게 */
    height: 150px;
    margin-right: 0;
    margin-bottom: 10px;
  }

  .research-info {
    text-align: center;
  }

  .research-name {
    font-size: 1.2rem;
  }

  .research-description {
    font-size: 0.9rem;
  }
}

