/* 기본 footer 스타일 */
.footer {
  background-color: #d9d9d93f;
  color: #333;
  text-align: center;
  padding: 15px;
  font-size: 0.9em;
  border-top: 1px solid #ccc;
  box-sizing: border-box;
  width: 100%;
  margin-top: auto; /* flex 레이아웃에서 footer를 하단으로 밀어내기 */
}

/* 링크 스타일 */
.footer a {
  color: #333;
  text-decoration: none;
}

.footer a:hover {
  text-decoration: underline;
}

/* 반응형 스타일 - 화면 너비가 768px 이하일 때 */
@media (max-width: 768px) {
  .footer {
    font-size: 0.8em;
    padding: 10px;
  }

  .footer p {
    line-height: 1.4;
    margin: 5px 0;
  }

  .footer p:last-child {
    display: flex;
    flex-direction: column;
  }

  .footer p a {
    margin: 2px 0;
  }
}

/* 반응형 스타일 - 화면 너비가 480px 이하일 때 */
@media (max-width: 480px) {
  .footer {
    font-size: 0.7em;
    padding: 8px;
  }

  .footer p:last-child {
    font-size: 0.8em;
  }
}
