/* Login.css */
.login-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh; /* 화면 전체 높이 */
    background-color: #f7f7f7;
  }
  
  .login-page h2 {
    margin-bottom: 20px;
    font-size: 1.8em;
    color: #333;
  }
  
  .login-page input {
    width: 300px;
    padding: 10px;
    margin: 10px 0;
    font-size: 1em;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .login-page button {
    width: 320px;
    padding: 10px;
    font-size: 1em;
    color: white;
    background-color: #800000; /* 고려대 색상 */
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .login-page button:hover {
    background-color: #a00000;
  }
  