.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.footer {
  background-color: #e0e0e0;
  color: #333;
  text-align: center;
  padding: 15px;
  font-size: 0.9em;
  border-top: 1px solid #ccc;
  box-sizing: border-box;
  width: 100%;
  margin-top: auto; /* flex 레이아웃에서 footer를 하단으로 밀어내기 */
}

.main-content {
  flex: 1;
}

/* 트랜지션 그룹 스타일 */
.transition-group {
  position: relative;
}

.transition-wrapper {
  position: absolute;
  width: 100%;
}

/* 페이지 전환 애니메이션 */
.fade-enter {
  opacity: 0;
  transform: translateY(20px);
}

.normal {
  position: relative;
}

.sticky {
  position: fixed;
  height: 60px;
  z-index: 9;
}



.fade-enter-active {
  opacity: 1;
  transform: translateY(0);
  transition: opacity 300ms ease-in, transform 300ms ease-in;
}

.fade-exit {
  opacity: 1;
  transform: translateY(0);
}

.fade-exit-active {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 300ms ease-out, transform 300ms ease-out;
}

@font-face {
  font-family: 'CustomFont';
  src: url('고려대 백주년기념체/Roboto-Black.ttf') format('truetype');
}

* {
  font-family: 'CustomFont', sans-serif;
}