.members-section {
  width: 100%;
  text-align: center;
  background-color: #f4f4f4;
}

.main-image {
  width: 100%;
  height: auto;
  max-height: 400px;
  object-fit: cover;
  margin-bottom: 20px;
}
.sub-section{
  font-size: 2rem;
}

.professors-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
}

.members-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.member {
  display: flex;
  align-items: center;
  width: 45%; /* Two items per row for members */
  max-width: 850px;
  background-color: white;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: left;
  border-radius: 8px;
}

.professor{
  display: flex;
  align-items: center;
  width: 45%;;
  background-color: white;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: left;
  padding: 15px;
  border-radius: 8px;
}

.profile-image {
  width: 150px;
  height: 150px;
  object-fit: cover;
  border-radius: 8px;
  margin-right: 20px;
}

.research-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.view-more-button {
  margin-bottom: 20px;
  padding: 10px 20px;
  background: #a10e2d;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

/* 반응형 스타일 */
@media (max-width: 768px) {
  .professor, .member {
    width: 90%;
  }

  .profile-image {
    width: 120px;
    height: 120px;
    margin-right: 15px;
  }

  .main-image {
    max-height: 250px;
  }
}



@media (max-width: 480px) {
  .professor, .member {
    width: 100%;
  }

  .profile-image {
    width: 100px;
    height: 100px;
    margin-right: 10px;
  }

  .main-image {
    max-height: 200px;
  }
}
